$light-gray: #EEEEEE;
.button_green{
    background: linear-gradient(90deg, #13493F 0%, #0C7B1B 100%) !important;
    position:absolute;
    margin-top: 35px;
    width: 164px;
    height: 48px;
    border-radius: 4px;
    border: none;
}
